.offer{
    display: flex;
    padding: 0px 130px;
    margin: 50px auto;
    width: 75%;
    background: whitesmoke;
    height: 100vh;
    .offers-left{
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: center;
        .offer-h1{
            font-size: 80px;
            color: black;
            font-weight: 600;
        }
        .para-offer{
            font-size: 22px;
            color: black;
            font-weight: 600;
            margin-top: 20px;
        }
        button{
            font-family: 'Poppins';
            font-weight: 600;
            margin-top: 30px;
            width: 300px;
            height: 60px;
            border: none;
            background: red;
            border-radius: 30px;
            color: white;
            font-size: 20px;
            cursor: pointer;
        }
    }
    .offers-right{
        flex: 1 1;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: flex-end;
    }
    
}





@media screen and (max-width:1280px) {
    .offer{
        padding: 0px 100px;
        width: 75%;
        height: 80vh;
        .offers-left{
            .offer-h1{
                font-size: 60px;
            }
            .para-offer{
                font-size: 20px;
            }
            button{
                width: 280px;
                height: 55px;
                border-radius: 30px;
                font-size: 18px;
            }
        }
        .offers-right{
            img{
                width: 300px;
            }
        }
        
    }

}

