.product-display{
    display: flex;
    align-content: center;
    gap: 40px;
    margin: auto;
    width: 80%;
    .product-display-left{
        display: flex;
        gap: 20px;
        .product-display-img-list{
        display: flex;
        flex-direction: column;
        width: 100px;
        }
        .product-display-img-big{
            img{
                width: 400px;
            }
        }
    }
    .product-display-right{
        display: flex;
        flex-direction: column;
        gap: 18px;
        .product-display-right-name{
            font-size: 30px;
            font-weight: 600;
        }
        .product-display-right-star{
            display: flex;
            flex-direction: row;
            align-content: center;
            align-items: center;
            gap: 5px;
            p{
            font-size: 20px;
            margin-left: 10px;
            font-weight: 50;
            }
        }
        .product-display-price{
            display: flex;;
            align-content: center;
            align-items: center;
            gap: 35px;
            .new-price{
                font-size: 20px;
                font-weight: 700;
                color: #ff0000cc;
            }
            .old-price{
                font-size: 20px;
                font-weight: 300;
                color: grey;
                text-decoration: line-through;
            }
        }
        .select-size{
            font-size: 20px;
            margin: 10px 0px;
            font-weight: 500;
            p{
                font-size: 20px;
                margin: 10px 0px;
                font-weight: 500;
            }
            .select-size-predict{
                display: flex;
                gap: 30px;
                align-content: center;
                align-items: center;
                .vary-size{
                    width: 50px;
                    background: whitesmoke;
                    display: flex;
                    height: 40px;
                    align-items: center;
                    flex-direction: row;
                    justify-content: center;
                }
                .vary-size:active{
                    background: #ff8800d1;
                }
            }
        }
        .add-to-cart-btn{
            .btn{
                border: none;
                outline: none;
                width: 200px;
                height: 60px;
                background: red;
                color: white;
                font-weight: 600;
                font-size: 20px;
                cursor: pointer;
            }
        }
        .category-tag{
            text-transform: capitalize;
        }
    }

}

@media only screen and (max-width:1280px) {
    .product-display{
        gap: 35px;
        .product-display-left{
            gap: 17px;
            .product-display-img-list{
            width: 88px;
            }
            .product-display-img-big{
                img{
                    width: 353px;
                }
            }
        }
        .product-display-right{
            flex-direction: column;
            gap: 15px;
            .product-display-right-name{
                font-size: 24px;
            }
            .product-display-right-star{
                gap: 4px;
                p{
                font-size: 18px;
                margin-left: 10px;
                }
            }
            .product-display-price{
                gap: 30px;
                .new-price{
                    font-size: 18px;
                }
                .old-price{
                    font-size: 18px;
                }
            }
            .select-size{
                font-size: 20px;
                p{
                    font-size: 20px;
                    margin:0px 0px 10px 0px;
                }
                .select-size-predict{
                    gap: 28px;
                    .vary-size{
                        width: 42px;
                        height: 35px;
                        font-size: 18px;
                    }
                }
            }
            .add-to-cart-btn{
                .btn{
                    width: 200px;
                    height: 60px;
                    font-size: 20px;
                    cursor: pointer;
                }
            }
        }
    
    }
}


