.footer{
    display: flex;
    flex-direction: column;
    margin-top: 50px;
    gap: 30px;
    align-items: center;
    background: whitesmoke;
    .footer-logo{
        display: flex;
        gap: 10px;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        .footer-logo-para{
            font-size: 30px;
            font-weight: 600;
            cursor: pointer;
        }
    }
    .footer-links{
        list-style: none;
        display: flex;
        gap: 20px;
        flex-wrap: wrap;
        justify-content: center;
        li{
            cursor: pointer;
        }
    }
    .social-icon{
        display: flex;
        gap: 25px;
        justify-content: center;
        align-items: center;
        .insta{
            cursor: pointer;
        }
        .pinterest{
            cursor: pointer;
        }
        .whatsapp{
            cursor: pointer;
        }
    }
    .copyrights{
        margin-bottom: 20px;
    }

}

@media screen and (max-width:1280px) {
    .footer{
        gap: 25px;
        .footer-logo{
            .footer-logo-para{
                font-size: 26px;
            }
        }
        .footer-links{
            gap: 20px;
            font-size: 16px;
        }
        .social-icon{
            gap: 25px;
        }
        .copyrights{
            font-size: 18px;
        }
    }
}

