.shop-category{
    width: 86%;
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin: 35px auto;
    .shopcategory-filter{
    display: flex;
    justify-content: space-between;
    align-items: center;
    .shopcategory-filter-para{
        font-size: 20px;
        font-weight: 600;
    }
    .shopcategory-filter-span{
        font-size: 20px;
        font-weight: 400;
        margin-right: 10px;
    }
    .shopcategory-btn{
        cursor: pointer;
        padding: 0px 10px;
        display: flex;
        align-content: center;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        gap: 10px;
        width: 130px;
        background: whitesmoke;
        height: 50px;
        border-radius: 35px;
    }
    }
    .shopcategory-products{
        display: flex;
        gap: 35px;
        flex-wrap: wrap;
    }
    .shopcategory-explore-more{
        display: flex;
        cursor: pointer;
        align-content: center;
        justify-content: center;
        align-items: center;
        flex-direction: row;
        background: whitesmoke;
        width: 150px;
        height: 50px;
        font-size: 16px;
        font-weight: 500;
        color: grey;
        border-radius: 50px;
        margin: 100px auto;
    }
}


@media screen and (max-width:1280px) {
    .shop-category{
        .shopcategory-filter{
        .shopcategory-filter-para{
            font-size: 18px;
        }
        .shopcategory-filter-span{
            font-size: 18px;
        }
        .shopcategory-btn{
            width: 100px;
            height: 40px;
            border-radius: 35px;
            font-size: 18px;
        }
        }
    }
}

@media screen and (max-width:1020px) {
    .shop-category{
        .shopcategory-filter{
        .shopcategory-filter-para{
            font-size: 16px;
        }
        .shopcategory-filter-span{
            font-size: 16px;
        }
        .shopcategory-btn{
            font-size: 16px;
        }
        }
    }
}

@media screen and (max-width:800px) {
    .shop-category{
        .shopcategory-filter{
        .shopcategory-filter-para{
            font-size: 14px;
        }
        .shopcategory-filter-span{
            font-size: 14px;
        }
        .shopcategory-btn{
            font-size: 14px;
        }
        }
    }
}

