.item{
    width: 300px;
    margin: auto;
    img{
        width: 300px;
    }
    p{
        font-size: 18px;
        margin: 5px 0px;
    }
    .item-price{
        display: flex;
        gap:15px;
        .new-price{
            color:black;
            font-weight: 700;
            font-size: 16px;
        }
        .old-price{
            color:grey;
            font-weight: 500;
            font-size: 16px;
            text-decoration: line-through;
        }
    }
    
}
.item:hover{
    transform:scale(1.05);
    transition: 0.6s;
}
@media only screen and (max-width:1280px) {
    .item{
        width: 260px;
        img{
            width: 260px;
        }
        p{
            font-size: 16px;
        }
        .item-price{
            gap:13px;
            .new-price{
                font-size: 14px;
            }
            .old-price{
                font-size: 14px;
            }
        }
        
    }
  }

